@import '../../styles/index.scss';

main {
  width: 100vw;
  height: 90vh;
  color: $color-main_01;
  display: flex;

  .mapContainer {
    width: 50%;
    overflow: hidden;
    margin: 1rem;
    border-radius: 10px;
    position: relative;
  }

  .main {
    width: 50%;
    height: 100%;
    border: 1px solid red;
    color: $color-main_01;
    padding: 0 20px;

    .navbar {
      width: 100%;
      height: 230px;
      background: $nav-color;
      border-radius: 0 0 8px 8px;
      position: relative;
      border-bottom: 1px solid #ff4f4f;
    }
  }
}

.categoryBar {
  height: 3rem;
  display: flex;
  justify-content: space-around;
  align-items: center;

  .category {
    display: flex;
    align-items: center;
  }
}

.cardsContainer {
  // display: flex;
  // flex-wrap: wrap;
  width: max-content;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-gap: 5px;
}
